import { useEffect, useState } from "react"
import storage from "../../utils/storage"

const Modal = () => {
  const [isShow, setIsShow] = useState<boolean>(true)

  useEffect(()=>{
    const today = new Date().getTime()
    const popupState = storage.getItem('popup', true)
    if(today > popupState){
      storage.removeItem('popup')
      setIsShow(true)
    }else{
      setIsShow(false)
    }
  },[])

  const closeModal = () =>{
    setIsShow(false)
  }

  const hideModal = () => {
    const today = new Date()
    storage.setItem('popup', new Date(today.setDate(today.getDate()+1)).getTime())
    setIsShow(false)
  }

  return (
    <div id="popup_layer">
      {
        isShow? 
        <div >
            <div id="layer_popup" className="row">
           
              <h1>신주발행 공고 (주주배정)</h1>
              <div className="layer_scroll">
                <h2>제목 : 신주발행 공고 (주주배정 유상증자)</h2>
                <p style={{lineHeight: '15px'}}>상법 제416조에 의거 2024년 2월 5일에 개최한 당사 이사회에서 아래와 같이 신주발행을 결의하였음을 공고합니다. </p>

            <p style={{textAlign:'center'}}>-<span style={{padding:'0px 10px'}}>아&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;래</span>-</p>
            <p style={{lineHeight: '5px'}}>1. 신주식의 종류와 수 : 보통주식 6,000,000주</p>
            <p style={{lineHeight: '5px'}}>2. 신주의 발행 방법: 주주배정방식 유상증자</p>
            <p style={{lineHeight: '5px'}}>3. 신주식의 발행가액 : 1주당 금 500원 (액면가: 1주당 금100원)</p>
            <p style={{lineHeight: '5px'}}>4. 신주배정기준일: 2024년 2월 22일</p>
            <p style={{lineHeight: '15px'}}>5. 신주의 배정방법 : 당사 정관 제9조 제1항 에 의거 회사의 주주는 신주발행에 있어 소유한 주식 수에 비례하여 신주의 배정을 받을 권리를 가지며, 주주는 인수권을 포기할 수 있으며, 이 경우 인수포기한 주식은 별도의 실권주 이사회 결의를 거쳐서 처리한다.</p>
            <p style={{lineHeight: '5px'}}>6. 신주청약일: 2024년 3월 25일</p>
            <p style={{lineHeight: '5px'}}>7. 청약증거금 납임: 청약대금전액</p>
            <p style={{lineHeight: '5px'}}>8. 납입기일: 2024년 3월 29일 </p>
            <p style={{lineHeight: '5px'}}>9. 납입은행 및 지점명 : 주식회사국민은행 강남구청역지점</p>
            <p style={{lineHeight: '5px'}}>10. 신주인수권에 관한 사항: 해당사항 없음.</p>
            <p style={{lineHeight: '15px'}}>11. 실권주의 처리: 당사의 신주인수권을 가진 일부 주주가 신주인수권을 포기하여 실권주가 발생할 경우 별도의 실권주 이사회 결의를 거쳐서 처리한다.</p> 
            <p style={{lineHeight: '5px'}}>12. 기타 신주발행에 관해 필요한 절차사항은 대표이사에게 일임한다.</p>
                <p style={{ lineHeight: '5px' }}>&nbsp;</p>    
            
             <h2 style={{textAlign:'center'}}>신주발행기준일 및 주주명부 폐쇄기간 설정 공고</h2>
             <p style={{lineHeight: '15px'}}>상법 제354조 및 당사 정관 제9조에 의거 신주배정을 위하여 아래와 같이 기준일을 설정하고 주식의 명의개서, 질권등록 및 그 변경과 말소, 신탁재산의 표시 및 말소를 정지하오니 양지하시기 바랍니다.</p>
                    

         <p style={{textAlign:'center'}}>-<span style={{padding:'0px 10px'}}>아&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;래</span>-</p>
         <p style={{lineHeight: '5px'}}>1. 신주배정 기준일(주주확정일): 2024년 2월 22일</p>
         <p style={{lineHeight: '5px'}}>2. 명의개서 정지기간: 2024년 2월 23일 ~ 2024년 2월 26일</p>

            <p style={{ textAlign: 'center', fontWeight: 'bold' }}>2024년 2월 15일
            </p>
            <p style={{ textAlign: 'center', fontWeight: 'bold' }}>주식회사 메타록 대표이사 조영욱(직인생략)
            </p>
              </div>
              {/* <img src={`${process.env.PUBLIC_URL}/images/logo/logo.png`} /> */}
              <div className="medal_btn">
              <button className="btn_one" onClick={hideModal}>하루동안 보지 않기</button>
              <button className="close_btn" onClick={closeModal}>닫기</button>
              </div>
          </div >
        </div >
        :null
      }
    </div >
  )
}

export default Modal